import clsx from "clsx";
import React, { useState } from "react";
import "../../styles/tailwind.css";

export interface ButtonProps {
  backgroundColor?: string;
  width?: number | string;
  height?: number | string;
  label: string;
  onClick?: () => void;
  rounded?: boolean;
  className?: string;
  color?: string;
  hoverColor?: string;
  type: "fullcolor" | "text" | "outline";
}

/** Primary UI component for user interaction */
export const Button = ({
  width,
  height,
  backgroundColor,
  label,
  rounded,
  className,
  color,
  hoverColor,
  type,
  ...props
}: ButtonProps) => {
  const [hover, setHover] = useState(false);

  return (
    <button
      onMouseOver={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
      type="button"
      className={`${className} flex items-center justify-center border-solid rounded-lg border-2 px-3 py-1 border-main-color text-white bg-main-color font-bold ${clsx(
        {
          // hover
          [`border-base-color bg-base-color`]: hover && type !== "text",
          [`border-${hoverColor}`]: hover && hoverColor && type !== "text",
          [`bg-${hoverColor}`]: hover && hoverColor && type === "fullcolor",

          // type fullcolor
          [`text-${color}`]: type === "fullcolor" && color,

          // type text
          [`text-${color}`]: type === "text" && color,

          // type outline
          [`text-${color}`]: type === "outline" && color,
        }
      )}`}
      style={{
        backgroundColor,
        width: width,
        height: height,
        WebkitUserSelect: "none",
        MozUserSelect: "none",
        msUserSelect: "none",
        userSelect: "none",
      }}
      {...props}
    >
      {label}
    </button>
  );
};
